.fcontainer {
  /* border: 2px solid gray; */
  display: flex;
  flex-wrap: wrap;
  position: relative;
  top: 100px;
}

.fitem {
  border: 2px solid black;
  margin: 10px;
  height: 380px;
  width: 307px;
  position: relative;
  /* left: 68px; */
  border-radius: 20px;
  border: none;
  box-shadow: 0px 0px 20px 0px rgb(217, 212, 212);
  transition: 1s;
}
.fitem:hover {
  margin-top: -10px;
}
.myblog {
  position: relative;
  left: 75px;
  top: 100px;
  width: 21%;
  height: 380px;
  border-radius: 20px;
  border: none;
  box-shadow: 0px 0px 20px 0px rgb(217, 212, 212);
  margin-left: 10px;
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
}
.h4profile {
  font-weight: 700;
  font-size: 26px;
  position: relative;
  top: 25px;
  left: 77px;
}
.dashboard {
  position: relative;
  top: 40px;
  left: 78px;
  font-size: 16px;
  text-decoration: none;
  color: black;
  opacity: 0.9;
  font-weight: 500;
}
.dot1 {
  position: relative;
  top: 39px;
  left: 85px;
  opacity: 0.4;
}
.user {
  position: relative;
  top: 40px;
  left: 98px;
  font-size: 16px;
  text-decoration: none;
  color: black;
  opacity: 0.9;
  font-weight: 500;
}
.dot2 {
  position: relative;
  top: 39px;
  left: 108px;
  opacity: 0.4;
}
.ui {
  position: relative;
  top: 15px;
  left: 274px;
  font-size: 16px;
  text-decoration: none;
  color: black;
  opacity: 0.4;
  font-weight: 500;
}
.pluspostbutton {
  background-color: #f45ca5;
  width: 130px;
  font-weight: 500;
  position: relative;
  float: right;
  right: 120px;
  bottom: 65px;
}
.addicon {
  position: relative;
  bottom: 2px;
  right: 8px;
}
.blogsearchicon {
  position: relative;
  top: 38px;
  opacity: 0.5;
  left: 26px;
  z-index: 1;
}
.searchpost {
  /* border:none ; */
  outline: none;
  height: 40px;
  width: 200px;
  position: relative;
  left: 55px;
  top: 40px;
  border-radius: 5px;
  border-color: rgb(176, 176, 176);
}
.searchpost::placeholder {
  position: relative;
  left: 50px;
  opacity: 0.5;
  font-weight: 500;
}
.blogselect {
  position: relative;
  float: right;
  left: 5px;
  top: 38px;
  border-radius: 4px;
  height: 40px;
  width: 90px;
  border-color: rgb(176, 176, 176);
  font-weight: 500;
  outline: none;
}

.myblog:hover {
  position: relative;
  top: 85px;
  transition: top 0.5s;
}
.myblog2 {
  position: relative;
  top: 20px;
}

.blog4img1 {
  height: 40px;
  width: 40px;
  border-radius: 30px;
  z-index: 1;
