.logincard {
  height: 590px;
  border-radius: 20px;
  position: relative;
  margin-top: 1rem;
  /* top: 16px; */
  width: 100%;
  box-shadow: 0px 10px 10px 10px rgba(112, 144, 176, 0.12);
  border: none;
}

.logincard h1 {
  position: relative;
  top: 18%;
  margin-left: 1rem;
  /* left: 45px; */
  font-family: "public sans", sans-serif;
  font-size: 32px;
}

.loginlogo {
  height: 40px;
  width: 40px;
  position: relative;
  top: 45px;
  left: 45px;
  font-family: "public sans", sans-serif;
  line-height: 1.5;
  border-radius: 25px;
}

.loginbook {
  position: relative;
  top: 180px;
}

.logindiv8 {
  height: 100vh;
  /* background-color: #ff7bbb; */
}

.logincreateaccount {
  font-size: 0.875rem;
  font-family: "Public Sans", sans-serif;
  font-weight: 400;
  position: relative;
  left: 750px;
  top: 40px;
  font-size: 13.8px;
}

