  font-weight: 700;
  font-size: 1.25rem;
  font-family: "Public Sans", sans-serif;
  position: relative;
  top: 122px;
  left: 242px;
  font-size: 24px;
}

.loginenterdetail {
  font-size: 1.1rem;
  font-family: "Public Sans", sans-serif;
  font-weight: 400;
  color: rgb(99, 115, 129);
  position: relative;
  top: 125px;
  left: 243px;
}

