  height: 55px;
  width: 480px;
  border-radius: 10px;
  position: fixed;
  top: 260px;
  left: 764px;
}

.loginemaillabel {
  color: red;
  font-weight: 500;
  font-size: 13px;
  position: fixed;
  top: 313px;
  left: 768px;
}

.loginpwd {
  height: 55px;
  width: 480px;
  border-radius: 10px;
  position: fixed;
  top: 334px;
  left: 764px;
}

.loginpwdlabel {
  color: red;
  font-weight: 500;
  font-size: 13px;
  position: fixed;
  top: 386px;
  left: 768px;
}

.loginsubmit {
  height: 50px;
  width: 480px;
  border-radius: 10px;
  position: fixed;
  top: 435px;
  left: 765px;
  background-color: #f45ca5;
  border: none;
}

.loginsubmit:hover {
  background-color: #ff7bbb;
}

.loginremember {
  position: fixed;
  top: 417px;
  left: 772px;
}

#rme {
  font-weight: 400;
  font-family: "Public Sans", sans-serif;
  position: fixed;
  top: 413px;
  left: 790px;
}

#loginforgot {
  font-weight: 600;
  line-height: 1.57143;
  font-size: 14px;
  font-family: "Public Sans", sans-serif;
  text-decoration: none;
  position: fixed;
  top: 413px;
  left: 1117px;
}
input[type="checkbox"] {
  accent-color: #f45ca5;
}

